var slides = $('.slide');
var current = 0;
var interval;

var stop = function stop() {
  clearInterval(interval);
};

var next = function next() {
  slides[current].classList.remove('active', 'beforeEnd'); // stop current video

  var video = $('video', slides[current])[0];

  if (video) {
    video.pause();
    video.currentTime = 0;
  }

  current = slides[current + 1] ? current + 1 : 0;
  slides[current].classList.add('active'); // play next video

  video = $('video', slides[current])[0];

  if (video) {
    video.play();

    if (slides[current].classList.contains('slide-video')) {
      stop();

      video.onended = function () {
        next();
        play();
      };
    }
  }
};

var play = function play() {
  interval = setInterval(function () {
    slides[current].classList.add('beforeEnd');
    setTimeout(next, 425);
  }, speed * 1000 - 425);
};

if (slides[1]) {
  play();
}